import React, {useEffect, useState} from "react";
import DOMPurify from 'dompurify'
import PopUp from "./popup";

function InputArea(props){
    const [filteredSuggestions, setFilteredSuggestions] = useState([]);
    const [showSuggestions, setShowSuggestions] = useState(false);
    const [searchTerm, setSearchTerm] = useState("");
    const [showPopup, setShowPopup] = useState(false);
    const [popupMessage, setPopupMessage] = useState('');

    //list of coins

    function handleChange(event){
      //Event.preventDefault()
      let listOfCoins = props.listOfCoins
      let userInput = event.target.value
      setSearchTerm(userInput);

      if (userInput.length > 0 && listOfCoins) {
        const filtered = listOfCoins.filter(suggestion =>
          ((suggestion.name).toLowerCase()) == (userInput.toLowerCase()) ||  ((suggestion.symbol).toLowerCase()) == (userInput.toLowerCase())
        );
        // setFilteredSuggestions(filtered.slice(0,5));
        setFilteredSuggestions(filtered)
        setShowSuggestions(true);
      } else {
        setShowSuggestions(false);
      }
    };

    function handleSuggestionClick(suggestion){
      performSpecificSearch(suggestion.id)
      setShowSuggestions(false)
    }
    function performSpecificSearch(coin = null){
      if(coin || searchTerm.trim()){
        const cleanSearch = DOMPurify.sanitize(searchTerm)
        props.searchCoin(coin ?? cleanSearch)
        setSearchTerm("")
      }else{
        console.log('Please enter a valid input')
      }
    };
    function searchTopCoins(){
        props.search10(100)
    }
    function handleKeyPress(event){
      if (event.key === "Enter") {
        setShowSuggestions(false)
        performSpecificSearch();
      }
    };
    function handleSearchButtonClick() {
      if (!searchTerm.trim()) {
        // Optionally, provide feedback to the user about the need to enter a search term.
        setPopupMessage('Please Insert A Valid Search Term');
        setShowPopup(true);
        return; // Exit the function if searchTerm is empty or contains only whitespace
      }
      
      performSpecificSearch();
    }
    //props.listOfCoins
    //make suggestions based on what the user is inputting
    //<div onClick = {getTestCoins}><Button variant="contained">Top 10 Coins</Button></div>
    return (
      
      <div className="search-container">
      <PopUp
        show={showPopup}
        message={popupMessage}
        onClose={() => setShowPopup(false)}
      />
      <div className = "searchBarAndSuggestions">
        <input
          type="text"
          placeholder="Search By Name.."
          className="search-input"
          value={searchTerm}
          onChange={handleChange}
          onKeyDown={handleKeyPress}
        />
        {showSuggestions && filteredSuggestions.length > 0 && (
        <ul className="suggestions">
          {filteredSuggestions.map((suggestion, index) => (
              <li key={index} onClick={() => handleSuggestionClick(suggestion)}>
                {suggestion.name} ({suggestion.symbol})
              </li>
            ))}
        </ul>
        )}
        </div>
        <button className="search-button" onClick={handleSearchButtonClick}>Search</button>
        <button className="reset-button" onClick={searchTopCoins}>Top Coins</button>
      </div>
    );
}
export default InputArea