import React, {useState ,useEffect} from "react";
import axios from 'axios';
import Footer from "./Footer";
import Header from "./Header";
import CoinCard from "./Card";
import Button from '@mui/material/Button';
import InputArea from "./InputArea";
import DescriptionCard from "./Description";
import PopUp from "./popup";
import { unstable_ClassNameGenerator } from "@mui/material";


function App() {
  const [cacheCoinList,setCacheCoinList] = useState({
    data: null,
    timestamp: 0
  })//useState for the list of coins


  const [cacheSpecCoin,setCacheSpecCoin] = useState({
    data: null,
    timestamp: 0
  })//use state for the data on a specific coin
  const [isDone10,setDone10] = useState(false)
  const [isDoneSpec,setDoneSpec] = useState(false)
  const [showPopup, setShowPopup] = useState(false);
  const [popupMessage, setPopupMessage] = useState('');
  const [listOfNameIDs, setListOfNameIDs] = useState([]);




  //update the list of the names of coins
  async function updateCoinList() {
    try {
      const response = await fetch('https://api.coingecko.com/api/v3/coins/list');
      const data = await response.json(); // Fixed: Added parentheses to call json()
      setListOfNameIDs([...data]); // Update state with fetched coin list
    } catch (error) {
      setPopupMessage('You have made too many requests. Please wait and try again.');
      setDone10(false)
      setDoneSpec(false)
      setShowPopup(true);
      console.error("Failed to fetch coin list:", error);
    }
  }
  useEffect(() => {
    try{
      updateCoinList();
      getCachedCoinList(6);
    }catch(error){
      //rate limit
      setPopupMessage('You have made too many requests. Please wait and try again.');
      setDone10(false)
      setDoneSpec(false)
      setShowPopup(true);
      console.log(error)
    }
  }, []);
/////////////////////////////////////////////////////////////////////////////////////////////
  //cache for the list of coins

  async function fetchDataFromAPICoinList() {
    try{
      const response = await fetch('https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=100&page=1&sparkline=false&locale=en');
      const data = await response.json();
      return data;
    }catch(error){
      setPopupMessage('You have made too many requests. Please wait and try again.');
      setDone10(false)
      setDoneSpec(false)
      setShowPopup(true);
      console.log(error)
      return
    }
  }
  // Cache object to store data and timestamp
  // Function that checks if we should make a new request or use cached data
  //call this function when you want data, if the data it old it will update it
  async function getCachedCoinList(numbOfCoins) {
    const oneMinute = 10 * 1000; // 10 seconds in milliseconds

    // Check if the cache is older than 1 minute
    if (Date.now() - cacheCoinList.timestamp > oneMinute) {
        // Cache is old or empty, fetch new data
        try {
            const data = await fetchDataFromAPICoinList();
            const filterData = data.filter((item,index) =>{
              return index < numbOfCoins
            })
            setCacheCoinList({
                data:filterData,
                timestamp: Date.now()
            });
            setDone10(true)
            setDoneSpec(false)
        } catch (error) {
            console.error('Error fetching data:', error);
            setPopupMessage('You have made too many requests. Please wait and try again.');
            setDone10(false)
            setDoneSpec(false)
            setShowPopup(true);
            console.log(error)
            // Optional: handle error, possibly by using old cache if it exists
        }
    }else{
    // Return cached data
    setPopupMessage('Showing cache of data, please wait 10 sounds and try again!');
    setShowPopup(true);
    setDone10(true)
    setDoneSpec(false)
    return cacheCoinList.data;
    }
  }
  //call function once to load some coins first
  ///////////////////////////////////////////////////////////////////////////////////////////////
  //make a cache for when a user asks for a specific coin

  async function fetchDataFromAPISpecCoin(nameOfCoin) { 
    
    //bitcoin
    //need to find the id of the coin from the name of it
    let idOfCoin = "nothing"
    if(!nameOfCoin || nameOfCoin == "" || nameOfCoin.SyntheticBaseEvent){ 
      // this means the user pressed search without a inputing a value
      //
    }else{
    let testArray = [...listOfNameIDs]; //make a second copy of the array
    let testArray2 = [...listOfNameIDs]; //make a second copy of the array

    listOfNameIDs.filter((item,index)=>{
      if ((item.name).toUpperCase() == nameOfCoin.toUpperCase() ){
        //match for the coin we are looking for
        idOfCoin = item.id
      }
    })
    
    if(idOfCoin === "nothing"){
      testArray.filter((item,index)=>{
        if ((item.symbol).toUpperCase() == (nameOfCoin).toUpperCase() ){
          //match for the coin we are looking for
          idOfCoin = item.id
        }
      })
    }
    if(idOfCoin === "nothing"){
      testArray2.filter((item)=>{
        if((item.id).toUpperCase() == (nameOfCoin).toUpperCase()){
          idOfCoin = item.id
        }
      })
    }}
    try{
      const response = await fetch('https://api.coingecko.com/api/v3/coins/' + idOfCoin);
      const data = await response.json();
      return data;
    }catch(error){
      setPopupMessage('You have made too many requests. Please wait and try again.');
      setDone10(false)
      setDoneSpec(false)
      setShowPopup(true);
      console.error(error)
      
    }
  }
  // Cache object to store data and timestamp
  // Function that checks if we should make a new request or use cached data
  //call this function when you want data, if the data it old it will update it
  async function getCachedSpecCoin(nameOfCoin) {
    const oneMinute = 60 * 1000; // 60 seconds in milliseconds
    // Check if the cache is older than 1 minute
    if (Date.now() - cacheSpecCoin.timestamp > oneMinute) {
        // Cache is old or empty, fetch new data
        try {
            const data = await fetchDataFromAPISpecCoin(nameOfCoin);
            if(data.error){
              setPopupMessage('Cannot find a coin by this name.');
              setShowPopup(true);
              return
            }
            setCacheSpecCoin({
                data:data,
                timestamp: Date.now()
            });
        } catch (error) {
          console.error('Error fetching data:', error);
            // Optional: handle error, possibly by using old cache if it exists
        }
    }else{
      //when a new request is made before the 60 second timeout
      //window.alert("too many requests")
      setPopupMessage('You have made too many requests. Please wait and try again.');
      setShowPopup(true);
    }
    
    // Return cached data
    setDone10(false)
    setDoneSpec(true)
    return cacheSpecCoin.data;
  }
  //////////////////////////////////////////////////////////////////////////////////////
  
  
  return ( 
    listOfNameIDs && <div className="container">
      <Header/>
      <InputArea
        search10 = {getCachedCoinList}
        searchCoin = {getCachedSpecCoin}
        listOfCoins = {listOfNameIDs}
      />
      <PopUp
        show={showPopup}
        message={popupMessage}
        onClose={() => setShowPopup(false)}
      />
      <div className = "content">
        {isDone10 && cacheCoinList.data != null && cacheCoinList.data.map((item,index)=>{
          return <CoinCard 
          key = {index} 
          img = {item.image}
          name = {item.name}
          symbol = {item.symbol} 
          price = {item.current_price}
          rank = {item.market_cap_rank} 
          price24h = {item.price_change_24h}
          percent24h = {item.price_change_percentage_24h}
          high24h = {item.high_24h}
          low24h = {item.low_24h}
          percentMarketCap = {item.market_cap_change_percentage_24h}
          />
        })}
        {isDoneSpec && <DescriptionCard
          name= {cacheSpecCoin.data.name}
          symbol = {cacheSpecCoin.data.symbol}
          img = {cacheSpecCoin.data.image.large}
          description = {cacheSpecCoin.data.description.en}
          marketCap = {cacheSpecCoin.data.market_cap_rank}
          price = {cacheSpecCoin.data.market_data.current_price.usd}
          athPrice={cacheSpecCoin.data.market_data.ath.usd}
          marketCapTotal = {cacheSpecCoin.data.market_data.market_cap.usd}
          pricechange24h = {cacheSpecCoin.data.market_data.price_change_24h}
          percent24h = {cacheSpecCoin.data.market_data.price_change_percentage_24h}
          percent7d = {cacheSpecCoin.data.market_data.price_change_percentage_7d}
          percent14d = {cacheSpecCoin.data.market_data.price_change_percentage_14d}
          percent30d = {cacheSpecCoin.data.market_data.price_change_percentage_30d}
          percent60d = {cacheSpecCoin.data.market_data.price_change_percentage_60d}
          percennt200d = {cacheSpecCoin.data.market_data.price_change_percentage_200d}
          percent1y = {cacheSpecCoin.data.market_data.price_change_percentage_1y}
          marketCapChange24 = {cacheSpecCoin.data.market_data.market_cap_change_24h}
          marketCapChange24Percent = {cacheSpecCoin.data.market_data.market_cap_change_percentage_24h}
          athDate = {cacheSpecCoin.data.market_data.ath_date.usd}
        />}
      </div>
      <Footer/>
    </div>
  );
}

export default App;
