import React from "react";
import ArrowDropUpIcon from '@mui/icons-material/ArrowDropUp';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
const BigNumber = require('bignumber.js');


function CoinCard(props){
    function formatNumber(value) {
        const number = new BigNumber(value);
        // Check if the number is too small and might be rounded to 0
        if (number.isZero()) {
            return '0.00'; // Adjust this based on your needs
        }
    
        let formattedNumber = number.toFixed();
        // Determine if the number is very small (but not zero)
        if (number.abs().isLessThan(1)) {
            // Find the first significant digit to avoid scientific notation
            let significantDigits = 0;
            for (let i = 2; i < formattedNumber.length; i++) {
                if (formattedNumber.charAt(i) !== '0') {
                    significantDigits = i - 1; // minus 1 to account for leading "0."
                    break;
                }
            }
            significantDigits += 2; // Target two significant figures
            formattedNumber = number.toFixed(significantDigits);
        } else {
            // For larger numbers, simply format to two decimal places
            formattedNumber = number.toFixed(2);
        }
    
        return formattedNumber;
    }

    return (
        <div className="card">
            <div className="card-top">
                <div className="card-text">
                    <h2>{props.name} ({props.symbol})</h2>
                    <h3>Market Cap Rank: {props.rank}</h3>
                    <p>$ {props.price} USD</p>
                </div>
                <div className="card-img">
                    <img src={props.img}/>
                </div>
            </div>
            <div className="card-bottom">
                <p style = { formatNumber(props.percent24h) >= 0 ? {color: "green"} : {color:"red"}}>Percent Change 24 Hours: {formatNumber(props.percent24h) }% {formatNumber(props.percent24h) >= 0 ? <ArrowDropUpIcon className="upArrow" />:<ArrowDropDownIcon className="downArrow"/>} <br/> </p>
                <p style = { formatNumber(props.price24h) >= 0 ? {color: "green"} : {color:"red"}}>Price Change 24 Hours: ${formatNumber(props.price24h)} USD { formatNumber(props.price24h) >= 0 ? <ArrowDropUpIcon className="upArrow"/>:<ArrowDropDownIcon className="downArrow"/>}<br/></p>
                <p>24 hour high: $ {formatNumber(props.high24h)} USD<br/></p>
                <p>24 hour Low: $ {formatNumber(props.low24h)} USD<br/></p>
            </div>
        </div>
    )
}export default CoinCard