import React from "react";
import DOMPurify from "dompurify";
import ArrowDropUpIcon from '@mui/icons-material/ArrowDropUp';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
const BigNumber = require('bignumber.js');

function DescriptionCard(props){
    function formatNumber(value) {
        const number = new BigNumber(value);
        // Check if the number is too small and might be rounded to 0
        if (number.isZero()) {
            return '0.00'; // Adjust this based on your needs
        }
    
        let formattedNumber = number.toFixed();
        // Determine if the number is very small (but not zero)
        if (number.abs().isLessThan(1)) {
            // Find the first significant digit to avoid scientific notation
            let significantDigits = 0;
            for (let i = 2; i < formattedNumber.length; i++) {
                if (formattedNumber.charAt(i) !== '0') {
                    significantDigits = i - 1; // minus 1 to account for leading "0."
                    break;
                }
            }
            significantDigits += 2; // Target two significant figures
            formattedNumber = number.toFixed(significantDigits);
        } else {
            // For larger numbers, simply format to two decimal places
            formattedNumber = number.toFixed(2);
        }
    
        return formattedNumber;
    }

    //sanitize the potenially ddangerous html
    const cleanDescription = DOMPurify.sanitize(props.description);
    //create a more readable date
    const date = new Date(props.athDate)
    const readableDate = date.toLocaleString();


    return(
        <div className = "fullDescCard">
            <div className = "fullDescCardTop">
                <div className = "fullDescCardLeft">
                    <h1>{props.name} ({props.symbol})</h1>
                    <h2>Current Price: ${formatNumber(props.price)} USD<br/></h2>
                    <h4>Market Cap Rank: {props.marketCap}<br/></h4>
                </div>
                <div className = "fullDescCardRight">
                    <img src={props.img}/>
                </div>
            </div>
            <div className = "fullDescCardBottom">
                <p style = { formatNumber(props.pricechange24h) >= 0 ? {color: "green"} : {color:"red"}}>24 hour Price Change: ${formatNumber(props.pricechange24h)} USD {formatNumber(props.pricechange24h) >= 0 ? <ArrowDropUpIcon className="upArrow" />:<ArrowDropDownIcon className="downArrow"/>}<br/></p>
                <p style = { formatNumber(props.percent24h) >= 0 ? {color: "green"} : {color:"red"}}>24 Hour Percent Change: {formatNumber(props.percent24h)}% {formatNumber(props.percent24h) >= 0 ? <ArrowDropUpIcon className="upArrow" />:<ArrowDropDownIcon className="downArrow"/>}<br/></p>
                <p style = { formatNumber(props.percent7d) >= 0 ? {color: "green"} : {color:"red"}}>7 Day Percent Change: {formatNumber(props.percent7d)}% {formatNumber(props.percent7d) >= 0 ? <ArrowDropUpIcon className="upArrow" />:<ArrowDropDownIcon className="downArrow"/>}<br/></p>
                <p style = { formatNumber(props.percent14d) >= 0 ? {color: "green"} : {color:"red"}}>14 Day Percent Change: {formatNumber(props.percent14d)}% {formatNumber(props.percent14d) >= 0 ? <ArrowDropUpIcon className="upArrow" />:<ArrowDropDownIcon className="downArrow"/>}<br/></p>
                <p style = { formatNumber(props.percent30d) >= 0 ? {color: "green"} : {color:"red"}}>30 Day Percent Change: {formatNumber(props.percent30d)}% {formatNumber(props.percent30d) >= 0 ? <ArrowDropUpIcon className="upArrow" />:<ArrowDropDownIcon className="downArrow"/>}<br/></p>
                <p style = { formatNumber(props.percent60d)  >= 0 ? {color: "green"} : {color:"red"}}>60 Day Percent Change: {formatNumber(props.percent60d)}% {formatNumber(props.percent60d) >= 0 ? <ArrowDropUpIcon className="upArrow" />:<ArrowDropDownIcon className="downArrow"/>}<br/></p>
                <p style = { formatNumber(props.percennt200d)  >= 0 ? {color: "green"} : {color:"red"}}>200 Day Percent Change: {formatNumber(props.percennt200d)}% {formatNumber(props.percennt200d) >= 0 ? <ArrowDropUpIcon className="upArrow" />:<ArrowDropDownIcon className="downArrow"/>}<br/></p>
                <p style = { formatNumber(props.percent1y)  >= 0 ? {color: "green"} : {color:"red"}}>1 Year Percent Change: {formatNumber(props.percent1y)}% {formatNumber(props.percent1y) >= 0 ? <ArrowDropUpIcon className="upArrow" />:<ArrowDropDownIcon className="downArrow"/>}<br/></p>
                <p>All Time High Price: ${formatNumber(props.athPrice)} USD<br/></p>
                <p>All Time High Date: {readableDate}<br/></p>
                <p>Total Market Cap: ${props.marketCapTotal} USD<br/></p>
                <div dangerouslySetInnerHTML={{ __html: cleanDescription }} /><br/>
            </div>
        </div>
    )
}
export default DescriptionCard